import React from 'react';
import {makeStyles} from '@material-ui/core/styles';

const SaveButtonPurple = ({disabled, click}) => {
  const classes = useStyles();
  return (
      <div className={disabled ? classes.saveButtonDisabled : classes.saveButton} onClick={click}>
          Сохранить
      </div>
  );
};

const useStyles = makeStyles((theme) => ({
  saveButton: {
    cursor: 'pointer',
    background: global.config.colors.purple,
    color: '#fff',
    width: '100px',
    textAlign: 'center',
    borderRadius: '10px',
  },
  saveButtonDisabled: {
    cursor: 'pointer',
    background: '#787477',
    color: '#fff',
    width: '100px',
    textAlign: 'center',
    borderRadius: '10px',
  }

}));

export default SaveButtonPurple;
