import Button from "@material-ui/core/Button";
import {makeStyles} from '@material-ui/core/styles';
import React, { useEffect } from 'react';
import {withRouter} from 'react-router-dom';
import {connect} from "react-redux";
import {setEndDate, setStartDate, setIsTodayButton} from "../../actions/dateActions";
import PropTypes from "prop-types";


const TodayButton = ({setStartDate, setEndDate, setIsTodayButton, children}) => {
    const classes = useStyles();

    //Назад возвращаем это состояние - это связано с заморочками с прыганием даты в календаре (он центрирует выбранную дату автоматомб
    // а нам этого не надо)
    const setIsTodayButtonBack = () => {
        setTimeout(() => {
            setIsTodayButton(false);
        }, 500)
    };

    const returnStartDate = () => {
        setStartDate(new Date());
        setEndDate(new Date());
        setIsTodayButton(true);
        setIsTodayButtonBack();
    }

    return (
        <div variant="outlined" color="primary" onClick={returnStartDate} className={classes.button}>
            {children}
        </div>
    )
}

const useStyles = makeStyles((theme) => ({
    button: {
        marginLeft: '10px',
        fontSize: '0.875rem',
        minWidth: '64px',
        boxSizing: 'border-box',
        fontFamily: 'Raleway',
        fontWeight: '500',
        lineHeight: '1.75',
        borderRadius: '4px',
        textTransform: 'uppercase',
        padding: '5px 15px',
        color: 'blue',
        borderColor: 'blue',
        border: '1px solid grey',
        display: 'inline-flex',
        [theme.breakpoints.down('xs')]: {
            color: '#fff',
            borderColor: '#fff'
        }
    }
}));


TodayButton.propTypes = {
    children: PropTypes.string.isRequired,
    setEndDate: PropTypes.any.isRequired,
    setStartDate: PropTypes.any.isRequired,
};

export default withRouter(connect(
    null,
    {setStartDate, setEndDate, setIsTodayButton}
)(TodayButton));
