import React, {useEffect, useRef, useState} from 'react';
import {makeStyles, withStyles} from '@material-ui/core/styles';
import {useSwipeable, Swipeable} from 'react-swipeable';
import ButtonMore from '../../../components/common/Icons/ButtonMore';
import IconButton from '@material-ui/core/IconButton';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import DeleteIcon from '@material-ui/icons/Delete';
import ReceiptSharp from '@material-ui/icons/ReceiptSharp';
import useWindowSize from '../../../hooks/useWindowSize';
import {StyledMenu} from '../../../components/materialize/StyledCustomMenu';
import {StyledMenuItem} from '../../../components/materialize/StyledCustomMenu';
import TransitionUp from '../../../components/common/TransitionUp';
import DialogModal from '../../../components/common/DialogModal';

const Transition = TransitionUp;
const MaterialItem = () => {
    const classes = useStyles();
    const [anchorEl, setAnchorEl] = useState(null);
    const [open, setOpen] = useState(false);
    let [showTrash, setShowTrash] = useState(false);
    const config = {
        delta: 10,                             // min distance(px) before a swipe starts
        preventDefaultTouchmoveEvent: false,   // preventDefault on touchmove, *See Details*
        trackTouch: true,                       // track touch input
        trackMouse: false,                     // track mouse input
        rotationAngle: 0,                      // set a rotation angle
    };
    const windowSize = useWindowSize();


    const handleClickDeletePrompt = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleCloseDeletePrompt = () => {
        setAnchorEl(null);
    };

    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    let handlers = useSwipeable({
        onSwipedLeft: (eventData) => {
            console.log('swapLeft');
            setShowTrash(false);
        },
        onSwipedRight: (eventData) => {
            console.log('swapRight');
            setShowTrash(true);
        },
        ...config,
    });

    {/* Если ширина экрана больше - то свайпы не сработают */
    }
    if (windowSize > 500) {
        handlers = [];
    }

    return (
        <div>

            <div className={classes.materialItem}>
                <div className={classes.itemName}>Бисквит белый</div>
                <div className={classes.weightPrice}>100р/кг</div>

                <IconButton onClick={handleClickDeletePrompt}>
                    <ButtonMore/>
                </IconButton>

            </div>

            <StyledMenu
                TransitionComponent={Transition}
                id="customized-menu"
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={handleCloseDeletePrompt}
                onClick={handleCloseDeletePrompt}
            >

                <StyledMenuItem onClick={handleOpen}>
                    <ListItemIcon color="primary">
                        <DeleteIcon fontSize="small"/>
                    </ListItemIcon>
                    <ListItemText primary="Удалить рецептуру"/>
                </StyledMenuItem>
                <StyledMenuItem>
                    <ListItemIcon>
                        <ReceiptSharp fontSize="small"/>
                    </ListItemIcon>
                    <ListItemText primary="Перенести в другой раздел"/>
                </StyledMenuItem>
            </StyledMenu>
            <DialogModal open={open} Transition={Transition}
                         handleCloseNo={handleClose} handleCloseYes={()=> {}} text="Удалить эту категорию?"/>
        </div>
    );
};

const useStyles = makeStyles((theme) => ({

    materialItem: {
        border: '1px solid #CDA85A',
        display: 'flex',
        justifyContent: 'space-around',
        alignItems: 'center',
        color: '#720D5D',
        fontSize: '2rem',
        height: '45px',
        marginTop: '4px',
        cursor: 'pointer',
        borderRadius: '5px',
        maxWidth: '360px',
        [theme.breakpoints.down('xs')]: {
            position: 'relative',
            left: 0,
            maxWidth: 'initial',
        },
    },
    itemName: {
        fontSize: '1rem',
        color: '#9A157D',
        fontWeight: 'bold',
    },
    weightPrice: {
        fontSize: '0.8rem',
        color: '#746871',
    },

}));

export default MaterialItem;
