import React, {useState} from 'react';
import styles from '../../css/components/downArrow.module.css';
import {makeStyles} from '@material-ui/core/styles';

const DownArrow = ({isOpen}) => {
  const [open, setOpen] = useState(false);

  const toggleClass = () => {
    setOpen(!open);
    isOpen(!open)
  }

  return (
      <div className={styles.arrowBlock}>
        <div className={styles.shadowBlock}></div>
        <a className={styles.arrowIcon} onClick={toggleClass}>
          <span className={`${styles.leftBar} ${open ? styles.leftBarOpen: ''}`}></span>
          <span className={`${styles.rightBar} ${open? styles.rightBarOpen: ''}`}></span>
        </a>
      </div>
  )
}

export default DownArrow;
