import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import tort from '../../../../images/tort_item_img.png';
import truck from '../../../../images/truck.svg';

const OrderItem = ({ blur }) => {
    const classes = useStyles();
    return (
        <div className={ blur ? classes.orderItemBlur :classes.orderItem} >
            <div className={classes.left}>
                <img src={tort} className={classes.tortImg} />
            </div>
            <div className={classes.center}>
                <div className={classes.clientName}>Настя Копай-Городская</div>
                <div className={classes.price}>Остаток 2000р  &#8226; ПО 1000р</div>
                <div className={classes.productName}>Торт &#8226; 2кг &#8226; Эстерхази</div>
            </div>
            <div className={classes.left}>
                <div className={classes.date}>28.02.20</div>
                <div className={classes.time}>Чт. 15:00</div>
                <div><img src={truck} className={classes.truckIcom} /></div>
            </div>
        </div>
    )
}

const useStyles = makeStyles((theme) => ({
    orderItem: {
        border: '1px solid #746871',
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        padding: '4%',
        maxWidth: '360px',
        marginTop: '1%',
        borderRadius: '5px',
        fontSize: '0.8rem',
        [theme.breakpoints.down('xs')]: {
            maxWidth: 'initial',
        },
    },
    orderItemBlur: {
        border: '1px solid #746871',
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        padding: '4%',
        maxWidth: '360px',
        marginTop: '1%',
        borderRadius: '5px',
        fontSize: '0.8rem',
        opacity: '0.4',
        [theme.breakpoints.down('xs')]: {
            maxWidth: 'initial',
        },
    },
    center: {
        textAlign:'center',
    },
    left: {
        textAlign:'center',
    },
    tortImg: {

    },
    clientName: {
        fontWeight: '700',
        fontSize: '0.9rem'
    },
    price: {
        fontSize: '0.7rem',
        color: '#9A157D'
    },
    productName: {
        fontSize: '0.7rem',
    },
    date: {
        color: '#FA1919',
        fontSize: '0.9rem',
        fontWeight: '700',
    },
    time: {
        color: '#FA1919',
        fontSize: '0.9rem',
        fontWeight: '700',
    }
}))

export default OrderItem;