import React from "react";
import {makeStyles} from "@material-ui/core/styles";

const CustomButtonFirst = ({children, customStyle}) => {
    const classes = useStyles(customStyle);



    return (
        <div className={classes.customButtonFirst}>
            {children}
        </div>
    )
}

const useStyles = makeStyles((theme, customStyle) => ({
    customButtonFirst: {
        border: '1px solid #a7a0a0',
        borderRadius: '15px',
        display: "inline-block",
        padding: "0 3%",
        fontWeight: "bold",
        boxShadow: "0 0 4px rgba(0,0,0,0.5)",
        fontSize: "0.9rem",
        marginRight: '2%',
        marginTop: '2%',
        cursor: 'pointer'
    }
}))

export default CustomButtonFirst;