import React, {useState} from 'react';
import axios from 'axios';
import {makeStyles} from '@material-ui/core/styles';
import TextFieldLite from '../../../../components/common/TextFieldLite';
import FormDeleteIcon from '../../../../components/common/Icons/FormDeleteIcon';
import {editNewMaterialStore} from '../../../../actions/ingredients/materialStore/materialStoreActions';
import useComponentVisible from '../../../../hooks/useComponentVisible';
import {useDispatch} from 'react-redux';

const MaterialStoreItem = ({elem, deleteLine, noedit, savedMaterialStore}) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  let [materialHints, setMaterialHints] = useState([]);
  const {ref, isComponentVisible, setIsComponentVisible} = useComponentVisible(
      true);

  const changeMaterialStoreName = (event) => {
    if (!noedit) {
      if (event.target.value.length === 0) {
        setMaterialHints([]);
        setIsComponentVisible(false);
      }
      // если минимум 1 буквы набрал - делаем запрос
      if (event.target.value.length > 0) {
        axios.get('/materials?search_by_name=' +        /// ПОИСК ПО ИМЕНИ (ПОКА НЕТ API)  ///
            event.target.value.capitalize()).
            then(resp => setMaterialHints(resp.data)).
            catch(e => console.error(e));
        setIsComponentVisible(true);
      }

      dispatch(editNewMaterialStore({
        ...elem,
        title: event.target.value,
      }));
    }
  };

  const chooseMaterialStoreTitle = (event) => {
    dispatch(editNewMaterialStore({
      ...elem,
      id: parseInt(event.target.getAttribute('data-id')),
      title: event.target.innerText,
    }));
    setMaterialHints([]);
  };

  const changeMaterialStoreCount = (event) => {
    if (!noedit) {
      dispatch(editNewMaterialStore({
        ...elem,
        count: event.target.value ? parseInt(event.target.value) : 0,
      }));
    }
  };

  const changeMaterialStorePriceCents = (event) => {
    if (!noedit) {
      dispatch(editNewMaterialStore({
        ...elem,
        priceCents: event.target.value ? parseInt(event.target.value) : 0,
      }));
    }
  };

  return (
      <div className={classes.materialItem}>
        <div className={classes.materialNameFieldWrapper}>
          <TextFieldLite label="Материал" variant="outlined"
                         value={elem.title}
                         onChange={changeMaterialStoreName}
                         className={classes.ingredient}/>
          {materialHints.length > 0 && isComponentVisible ? (
              <div className={classes.materialHints} ref={ref}>
                <ul>
                  {materialHints.map(
                      (el) => <li className={classes.materialHintsLi}
                                  onClick={chooseMaterialStoreTitle}
                                  data-id={el.id}
                                  key={el.id}>{el.title}</li>)}
                </ul>
              </div>
          ) : (<div></div>)}
        </div>
        <TextFieldLite label="Количество" variant="outlined"
                       value={elem.count} onChange={changeMaterialStoreCount}
                       className={classes.count}/>
        <TextFieldLite label="Цена/шт" variant="outlined"
                       value={elem.price}
                       onChange={changeMaterialStorePriceCents}
                       className={classes.price}/>

        <FormDeleteIcon onClick={() => deleteLine(elem.id, savedMaterialStore,
            elem.nanoId)}/>
      </div>
  );
};

const useStyles = makeStyles((theme) => ({
  materialItem: {
    marginTop: '10px',
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  materialNameFieldWrapper: {
    position: 'relative',
  },
  materialHints: {
    position: 'absolute',
    top: '58px',
    background: '#fff',
    zIndex: '2',
    width: '100%',
    border: '1px solid #c2c0c0',
    borderTop: 'none',
  },
  materialHintsLi: {
    cursor: 'pointer',
  },
  date: {
    [theme.breakpoints.down('xs')]: {
      display: 'none',
    },
  },
  ingredient: {
    flexBasis: '40%',
    [theme.breakpoints.down('xs')]: {
      flexBasis: '46%',
    },
  },
  count: {
    flexBasis: '20%',
    [theme.breakpoints.down('xs')]: {
      flexBasis: '16%',
    },
  },
  price: {
    flexBasis: '20%',
    [theme.breakpoints.down('xs')]: {
      flexBasis: '22%',
    },
  },
  trash: {
    cursor: 'pointer',
  },

}));

export default MaterialStoreItem;
