import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import recepies from "../../../images/Recepies.svg";
import works from "../../../images/works.svg";
import DateRangeRounded from "@material-ui/icons/DateRangeRounded";
import ListItemIcon from "@material-ui/core/ListItemIcon";

const BottomFixedMenu = () => {
    const classes = useStyles();
    return (
        <div className={classes.bottomFixedMenu}>
            <a className={classes.bottomFixedMenuItem}>
                <img src={recepies} className={classes.recepiesIcon}/>
                <div>Календарь</div>
            </a>
            <a className={classes.bottomFixedMenuItem}>
                <img src={recepies} className={classes.recepiesIcon}/>
                <div>Рецептуры</div>
            </a>
            <a className={classes.bottomFixedMenuItem}>
                <img src={works} className={classes.prodIcon}/>
                <div>Продукция</div>
            </a>
        </div>
    )
}

const useStyles = makeStyles((theme) => ({
    bottomFixedMenu: {
        display: 'none',
        [theme.breakpoints.down('xs')]: {
            display: 'flex',
            justifyContent: 'space-around',
            color: '#69305d',
            fontSize: '0.8rem',
            padding: '7px',
            paddingTop: '10px'
        }
    },
    bottomFixedMenuItem: {
        [theme.breakpoints.down('xs')]: {
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center'
        }
    },
    prodIcon: {
        width: '25px'
    },
    recepiesIcon: {
        width: '25px',
        marginBottom: '4px'
    }
}))

export default BottomFixedMenu;