import {
    ADD_PRODUCT,
    EDIT_PRODUCT,
    DELETE_PRODUCT,
    ADD_STANDARDISATION,
    EDIT_LOOSES,
    EDIT_TEST_EXIT
} from '../../actions/types/Products/MakeProductTypes';

const initialState = {
    ProductName: '',
    productTotalWeight: 0,
    ProducLooseWeight: 0,
    looses: 0,
    exit: 0,
    testExit: 0,
    priceKg: 0,
    standardisation: {
        d: 0,
        h: 0,
        coverWeight: 0,
    },
    products: [], // [{id: 1, name: 'milk',percent: 0,weight: 1000}]
    testProducts: []
};


const calcPercent = (ProductsArray) => {
    let totalWeight = ProductsArray.reduce((sum, elem) => {
        return sum + parseInt(elem.weight);
    }, 0);
    let resultProducts = ProductsArray.map(item => {
        return {
            ...item,
            percent: Math.round((item.weight / totalWeight) * 100),
        };
    });

    return {
        totalWeight,
        resultProducts,
    };
};

const recountToNewWeight = (newWeight, ProductsArray) => {
    return ProductsArray.map((item) => {
        return  {
            ...item,
            weight: Number(((newWeight / 100) * item.percent).toFixed(0))
        }
    })
}
const calcPriceKg = (ProductsArray, exit) => {

    let priceByProduct = ProductsArray.map((item) => {
        if(!item.weight) return 0;
        return item.weight * item.price;
    });

    let totalSum = priceByProduct.reduce((sum, elem) => {
        return sum + parseInt(elem);
    }, 0);

    if(totalSum !== 0) {
        let totalOnegramPrice = totalSum / exit;
        return Number((totalOnegramPrice * 1000).toFixed(0));
    } else {
        return 0;
    }
};


const calcExit = (val, looses) => {
    
    if (looses !== 0) {
        return val - (val / 100 * looses);
    } else {
        return val;
    }
};

const makeProductReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_PRODUCT:
            let newProdArr = [...state.products , action.payload];
            const resultAdd = calcPercent(newProdArr);

            return {
                ...state,//старое состояние
                exit: calcExit(resultAdd.totalWeight, state.looses),//новый выход
                testExit: calcExit(resultAdd.totalWeight, state.looses),
                priceKg: calcPriceKg(resultAdd.resultProducts,
                    calcExit(resultAdd.totalWeight, state.looses)),
                productTotalWeight: resultAdd.totalWeight,//новый вес
                products: resultAdd.resultProducts, //новый выход
                testProducts: resultAdd.resultProducts,
            };
        case EDIT_PRODUCT:
            const filterProducts = state.products.filter(
                (item) => item.id !== (action.payload.id));
            const newproducts = [...filterProducts, action.payload];
            const resultEdit = calcPercent(newproducts);

            return {
                ...state,
                exit: calcExit(resultEdit.totalWeight, state.looses),
                testExit:  calcExit(resultEdit.totalWeight, state.looses),
                priceKg: calcPriceKg(resultEdit.resultProducts,
                    calcExit(resultEdit.totalWeight, state.looses)),
                productTotalWeight: resultEdit.totalWeight,
                products: resultEdit.resultProducts,
                testProducts: resultEdit.resultProducts,
            };
        case DELETE_PRODUCT:
            const resultproductsArr = state.products.filter(
                (elem) => elem.id !== action.payload);
            const resultDelete = calcPercent(resultproductsArr);
            return {
                ...state,
                exit: calcExit(resultDelete.totalWeight, state.looses),
                testExit: calcExit(resultDelete.totalWeight, state.looses),
                priceKg: calcPriceKg(resultDelete.resultProducts,
                    calcExit(resultDelete.totalWeight, state.looses)),
                productTotalWeight: resultDelete.totalWeight,
                products: resultDelete.resultProducts,
                testProducts: resultDelete.resultProducts,
            };
        case EDIT_LOOSES:
            return {
                ...state,
                exit: calcExit(state.productTotalWeight, action.payload),
                testExit:  calcExit(state.productTotalWeight, action.payload),
                priceKg: calcPriceKg(state.products,
                    calcExit(state.productTotalWeight, action.payload)),
                looses: action.payload,
            };
        case EDIT_TEST_EXIT:
            return {
                ...state,
                testExit: action.payload,
                testProducts: recountToNewWeight(action.payload, state.testProducts)
            };
        case ADD_STANDARDISATION:
            return {
                ...state,
                standardisation: action.payload,
            };
        default:
            return state;
    }
};

export default makeProductReducer;
