import axios from 'axios';

const DeleteCategoryList = async (id, getCategories, handleCloseDelete ) => {
    try {
        // именно так axios требует чтобы через delete было
        let config = {
            headers: {
                Authorization: 'Bearer ' + localStorage.getItem('token'),
            },
        };

        axios.delete('/categories/' + id, config).then(response => {
            getCategories();
            handleCloseDelete()
        }).catch(er => {
            console.error(er);
        });
    } catch (e) {
        console.error(e);
    }




};

export default DeleteCategoryList;