import  {ADD_NEW_CLIENT} from '../../actions/types/clients/makeClientTypes';

const initialState = {
  name: '',
  phone: null,
  address: null,
  socialNetworks: [],
  date: null,
  time: null
};

const makeClientsReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_NEW_CLIENT:
      return {
        ...state,
        ...action.payload
      };
    default:
      return state;
  }
}

export default  makeClientsReducer;
