import {
    SET_LOADING_PRODUCTS,
    SET_CURRENT_PRODUCT_CATEGORY,
    GET_SAVED_PRODUCTS,
} from '../../actions/types/Products/ProductListTypes';

const initialState = {
    savedProducts: [], //  те что уже сохранены у пользователя
    currentProductCategory: {   //категория с которой стартуем
        title: 'Торты', //test
        id: 1 //test
    },
    loadingProduct: false //загрузка(кружки)
};


const productListReducer = (state = initialState, action) => {
    switch (action.type) {
        case GET_SAVED_PRODUCTS:
            return {
                ...state,
                savedProducts: action.payload,
            };
        case SET_CURRENT_PRODUCT_CATEGORY:
            return {
                ...state,
                currentProductCategory: action.payload,
            };
        case SET_LOADING_PRODUCTS:
            return {
                ...state,
                loadingproduct: action.payload,
            };
        default:
            return state;
    }
};

export default productListReducer;