import {
    ADD_PRODUCT,
    EDIT_PRODUCT,
    DELETE_PRODUCT,
    ADD_STANDARDISATION,
    EDIT_LOOSES,
    EDIT_TEST_EXIT

} from '../types/Products/MakeProductTypes';

export const addProduct = (data) => async dispatch => {
    dispatch({
        type: ADD_PRODUCT,
        payload: data,
    });
};

export const editProduct = (data) => async dispatch => {
    dispatch({
        type: EDIT_PRODUCT,
        payload: data,
    });
};

export const deleteProduct = (id) => async dispatch => {
    dispatch({
        type: DELETE_PRODUCT,
        payload: id,
    });
};

export const editLooses = (data) => async dispatch => {
    dispatch({
        type: EDIT_LOOSES,
        payload: data,
    });
};
export const editTestExit = (data) => async dispatch => {
        dispatch({
            type: EDIT_TEST_EXIT,
            payload: data,
        });    
};