import React, {useState} from "react";
import Grid from "@material-ui/core/Grid";
import SummaryRightPanel from "./SummaryRightPanel";
import {makeStyles} from "@material-ui/core/styles";
import OrdersListBlock from "./OrdersListBlock";
import {useSwipeable, Swipeable} from 'react-swipeable';
import useWindowSize from "../../../../hooks/useWindowSize";


const OrdersRightBlock = () => {
    const config = {
        delta: 10,                             // min distance(px) before a swipe starts
        preventDefaultTouchmoveEvent: false,   // preventDefault on touchmove, *See Details*
        trackTouch: true,                      // track touch input
        trackMouse: false,                     // track mouse input
        rotationAngle: 0,                      // set a rotation angle
    };
    const classes = useStyles();
    const windowSize = useWindowSize();

    let [showMobileOrders, setShowMobileOrders] = useState(false);

    function togleBottomMenu(status) {
        setShowMobileOrders(status)
    }

    return (
        <Grid item className={classes.orderRightsBlock}>
            <SummaryRightPanel togleBottomMenu={togleBottomMenu}/>
            <OrdersListBlock showMobileOrders={showMobileOrders}/>
        </Grid>
    )
}

const useStyles = makeStyles((theme) => ({
    orderRightsBlock: {
        padding: '0 1%',
        maxWidth: '840px',
        flex: 1
    }
}))

export default OrdersRightBlock;


