import {
  ADD_NEWINGREDIENT,
  EDIT_NEWINGREDIENT,
  DELETE_NEWINGREDIENT,
  GET_SAVED_INGREDIENTS,
  SET_CURRENT_INGREDIENTS_CATEGORY,
  SET_LOADING_INGREDIENTS,
  DELETE_ALL_NEWINGREDIENT,
  SET_SAVE_BUTTON,
  EDIT_OLDINGREDIENT
} from '../../actions/types/ingredients/ingredientTypes';

const initialState = {
    savedIngredients: [], //  то что уже сохранены у пользователя
    savedEditedIngredients: [], //  то что уже сохранены у пользователя, которые он отредактировал
    newIngredients: [], // те что он новые вводит
    currentIngredientsCategory: {
        title: 'Основные продукты',
        id: 9
    },
    loadingIngredients: false, //спиннер
    activeSaveButton: false // если есть что сохранять, то кнопка активна
};
// массивы ингридиентов { title : 'Сахар', weight: 1, price: 100, price_per_gram: 10, created_at: 08.11.2020}

const ingredientsReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_NEWINGREDIENT:
            return {
                ...state,
                newIngredients: [...state.newIngredients, action.payload],
            };
        case EDIT_NEWINGREDIENT:
            const filterNewIngredients = state.newIngredients.filter(
                (item) => item.nanoId !== (action.payload.nanoId));
            return {
                ...state,
                newIngredients: [...filterNewIngredients, action.payload],
            };
        case DELETE_NEWINGREDIENT:
            if(action.payload) {
                const resultNewIngredients = state.newIngredients.filter(
                    (item) => item.nanoId !== (action.payload));
                return {
                    ...state,
                    newIngredients: resultNewIngredients,
                };
            } else {
                //удалить все
                return {
                    ...state,
                    newIngredients: [],
                };
            }
      case EDIT_OLDINGREDIENT:
          const filterSavedIngredients = state.savedIngredients.filter(
              (item) => item.id !== (action.payload.id));
        const filterSavedEditedIngredients = state.savedEditedIngredients.filter(
            (item) => item.id !== (action.payload.id));
          return {
            ...state,
            savedIngredients: [...filterSavedIngredients, action.payload],
            savedEditedIngredients: [...filterSavedEditedIngredients, action.payload],
          };
        case DELETE_ALL_NEWINGREDIENT:
          return {
            ...state,
            newIngredients: [],
          }
        case SET_SAVE_BUTTON:
          return {
            ...state,
            activeSaveButton: action.payload,
          }
        case GET_SAVED_INGREDIENTS:
            return {
                ...state,
                savedIngredients: action.payload,
            };
        case SET_CURRENT_INGREDIENTS_CATEGORY:
            return {
                ...state,
                currentIngredientsCategory: action.payload,
            };
        case SET_LOADING_INGREDIENTS:
            return {
                ...state,
                loadingIngredients: action.payload,
            };
        default:
            return state;
    }
};

export default ingredientsReducer;
