import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import SearchIcon from '@material-ui/icons/Search';


const SearchField = () => {
    const classes = useStyles();
    return (
        <>
            <div className={classes.SummaryRightPanelSearchLine}>
                <input type="text" placeholder='Поиск' className={classes.searchField}/>
                <SearchIcon className={classes.searchIcon}/>
            </div>
        </>
    )
}

const useStyles = makeStyles((theme) => ({
    SummaryRightPanelSearchLine: {
        position: 'relative',
        display: 'flex',
        justifyContent: 'center',
    },
    searchField: {
        backgroundColor: 'rgba(218, 218, 218, 0.5)',
        border: 'none',
        height: '29px',
        margin: '10px 0',
        paddingLeft: '40px',
        flexBasis: '100%',
        maxWidth: '500px',
        '&:focus': {
            outline: 'none'
        },
        [theme.breakpoints.down('xs')]: {
            maxWidth: 'initial',
        },
    },

    searchIcon: {
        fill: '#fff',
        position: 'absolute',
        top: '13px',
        left: '10px'
    }
}))

export default SearchField;