import React, {useEffect} from "react";
import {makeStyles} from "@material-ui/core/styles";
import {useDispatch} from "react-redux";
import {isBigScreenDrawerOpen} from "../../../actions/effectsActions";
import {isShowTopPanel} from "../../../actions/effectsActions";
import {CustomTabPurple, CustomTabsPurple} from "../../../components/materialize/CustomTabMobPurple";
import MakeProductFirstBlock from "./MakeProductFirstBlock";
import MakeProductSecondBlock from "./MakeProductSecondBlock";
import MakeProductThirdBlock from "./MakeProductThirdBlock";
import tabSetProps from "../../../components/materialize/smallElements/TabSetProps";
import TabPanel from "../../../components/materialize/TabPanel";
import useWindowSize from '../../../hooks/useWindowSize';
import GoldArrowBack from '../../../components/common/Icons/GoldArrowBack';
import IconButton from '@material-ui/core/IconButton';
import {NavLink} from "react-router-dom";
import ButtonMore from '../../../components/common/Icons/ButtonMore';

const MakeProductIndex = () => {
    const classes = useStyles();
    const dispatch = useDispatch();
    const [tabValue, setTabValue] = React.useState(0);
    const windowSize = useWindowSize();

    useEffect(() => {
        dispatch(isBigScreenDrawerOpen(false));
        if(windowSize < 668) {
            dispatch(isShowTopPanel(false));
        }
    }, [])

    const tabHandleChange = (event, newValue) => {
        setTabValue(newValue);
    };
    return (
        <div className={classes.makeRecipesTab}>
            <div>
                <div className={classes.topPurpleLine}>
                    <div className={classes.leftlineBlock}>
                    <NavLink exact to="/production"  >
                       <IconButton color = "secondary">
                            <GoldArrowBack/>
                       </IconButton>
                    </NavLink>
                            <div className={classes.plusIcon}>+
                             </div>
                            Начинка
                    </div>
                    <div className={classes.rightlineBlock}>
                        <div className={classes.rightlineCancelButton}>Отменить изменения</div>
                        <div className={classes.rightlineSaveButton}>
                            Сохранить
                                <div className={classes.moreButtonWrapper}><ButtonMore className={classes.moreButton} /></div>
                        </div>
                    </div>
                </div>
                <div className={classes.makeRecipesBlocks}>
                    <MakeProductFirstBlock/>
                    <div className={classes.secondAndThirdWrapper}>
                        <MakeProductSecondBlock/>
                        <MakeProductThirdBlock/>
                    </div>
                </div>
            </div>

            <div className={classes.mobile}>
                <CustomTabsPurple
                    value={tabValue}
                    onChange={tabHandleChange}
                    indicatorColor="primary"
                    textColor="primary"
                    variant="scrollable"
                    scrollButtons="auto"
                    aria-label="scrollable auto tabs example"
                >
                    <CustomTabPurple label="Рецептура" {...tabSetProps(0)} />
                    <CustomTabPurple label="Пересчёт" {...tabSetProps(1)} />
                    <CustomTabPurple label="Трудозатраты" {...tabSetProps(2)} />
                </CustomTabsPurple>
                <TabPanel value={tabValue} index={0}>
                    <MakeProductFirstBlock/>
                </TabPanel>
                <TabPanel value={tabValue} index={1}>
                    <MakeProductSecondBlock/>
                </TabPanel>
                <TabPanel value={tabValue} index={2}>
                    <MakeProductThirdBlock/>
                </TabPanel>
            </div>
        </div>
    )
}

const useStyles = makeStyles((theme) => ({
    makeRecipesTab: {
        width: '100%',
        marginTop: '65px',
            ['@media (max-width:668px)']: {
                marginTop: '0',
            }
    },
    moreButton: {

    },
    moreButtonWrapper: {
        marginLeft: '10px',
        '& .MoreVertIcon-root-72': {
            color: '#fff!important'
        },
        ['@media (min-width:668px)']: {
            display: 'none'
        }
    },
    desktop: {

    },
    mobile: {
        ['@media (min-width:668px)']: {
            display: 'none'
        }
    },
    topPurpleLine: {
        display: 'flex',
        justifyContent: 'space-between',
        backgroundColor: global.config.colors.purple,
        color: '#fff',
        padding: '6px 2%'
    },
    leftlineBlock: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
    },
    rightlineBlock: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
    },
    arrowIcon: {
        fill: '#fff',
        width: '20px',
        cursor: 'pointer'
    },
    plusIcon: {
        marginLeft: '7px',
        marginRight: '7px',
    },
    rightlineCancelButton: {
        marginRight: '7px',
        cursor: 'pointer',
        ['@media (max-width:668px)']: {
            display: 'none',
        }
    },
    rightlineSaveButton: {
        color: global.config.colors.purple,
        backgroundColor: '#fff',
        borderRadius: '20px',
        padding: '3px 12px',
        cursor: 'pointer',
        ['@media (max-width:668px)']: {
            background: 'none',
            color: '#fff',
            display: 'flex'
        }
    },
    makeRecipesBlocks: {
        display: 'flex',
        paddingTop: '30px',
        ['@media (max-width:668px)']: {
            display: 'none'
        }
    },
    secondAndThirdWrapper: {
        display: 'flex',
        flexBasis: '50%',
        ['@media (max-width:1300px)']: {
            flexWrap: 'wrap'
        }
    }
}))

export default MakeProductIndex;
