import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import CustomButtonFirst from "../../../materialize/smallElements/CustomButtonFirst";

const SummaryRightPanelBlockButtons = () => {
    const classes = useStyles();
    return (
       <div className={classes.summaryRightPanelBlockButtons}>
           <CustomButtonFirst>Доход</CustomButtonFirst>
           <CustomButtonFirst>Расход</CustomButtonFirst>
           <CustomButtonFirst>Оборот</CustomButtonFirst>
       </div>
    )
}

const useStyles = makeStyles((theme) => ({
    summaryRightPanelBlockButtons: {
        paddingBottom: '1%',
        [theme.breakpoints.down('xs')]: {
            display: 'none',
        }
    }
}))

export default SummaryRightPanelBlockButtons;