import React, { useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import MakeProductFirstBlockLeft from "./MakeProductFirstBlockLeft";
import MakeProductFirstBlockRight from "./MakeProductFirstBlockRight";

const MakeProductFirstBlock = () => {
  const classes = useStyles();

  return (
    <div className={classes.makeRecipesFirstBlock}>
      <div className={classes.blockInfo}>
        <h2 className={classes.h2}>Начинка</h2>
        <div>
          Соотношение рецептур, стандартизация начинки, выход с учётом
          потери и цена килограмма
        </div>
      </div>
      <div className={classes.blockContent}>
        <MakeProductFirstBlockLeft />
        <MakeProductFirstBlockRight />
      </div>
    </div>
  );
};

const useStyles = makeStyles((theme) => ({
  h2: {},
  makeRecipesFirstBlock: {
    flexBasis: "50%",
    marginLeft: "5%",
    maxWidth: "660px",
    marginRight: "2%",
  },
  blockInfo: {
    paddingRight: "0",
    paddingBottom: "15px",
    marginBottom: "20px",
    borderBottom: "1px solid #c1bdbdde",
  },
  blockContent: {
    display: "flex",
    justifyContent: "space-between",
      ['@media (max-width:1300px)']: {
          flexWrap: 'wrap',
          flexDirection: 'column-reverse'
      }
  },
}));

export default MakeProductFirstBlock;
