import React, {useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import TextFieldLite from '../../../components/common/TextFieldLite';
import PlusRoundButton from '../../../components/common/PlusRoundButton';
import SaveButtonPurple from '../../../components/common/SaveButtonPurple';
import {useDispatch, useSelector} from 'react-redux';
import {addClient} from '../../../actions/clients/makeClientActions';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';

const MakeOrderMakeClient = ({showSaved}) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [saveButtonDisabled, setSaveButtonDisabled] = useState(false);
  const [socialNetworks, setSocialNetworks] = useState(['1']);
  const client = useSelector(
      state => state.clients.makeClient);

  const clientChange = (event) => {
    dispatch(addClient({[event.target.name]: event.target.value}));
  };

  const clickHandle = () => {
    showSaved(true);
  };

  const addNetwork = () => {
    console.log();
    setSocialNetworks((prevState) => [...prevState, '']);
  };

  const networkChange = (event, i) => {
    setSocialNetworks((prevState) => {
      prevState[i] = event.target.value;
      return [...prevState];
    })
  }

  return (
      <div className={classes.makeClientBlock}>
        <TextFieldLite label="Имя"
                       required
                       size="small"
                       variant="outlined"
                       name="name"
                       className={classes.clientName}
                       type='text'
                       value={client.name}
                       onChange={clientChange}/>

        <TextFieldLite label="Дата"
                       required
                       size="small"
                       variant="outlined"
                       name="date"
                       className={classes.clientDate}
                       type='text'
                       value={client.date}
                       onChange={clientChange}/>

        <div className={classes.thirdLine}>
          <TextFieldLite label="Адрес"
                         required
                         size="small"
                         variant="outlined"
                         name="address"
                         className={classes.clientAddress}
                         type='text'
                         value={client.address}
                         onChange={clientChange}/>

          <TextFieldLite label="Время"
                         required
                         size="small"
                         name="time"
                         variant="outlined"
                         className={classes.clientTime}
                         value={client.time}
                         onChange={clientChange}
                         type='text'/>
        </div>
        <div className={classes.fourthLine}>
          <TextFieldLite label="Телефон"
                         required
                         size="small"
                         name="phone"
                         variant="outlined"
                         value={client.phone}
                         className={classes.clientPhone}
                         onChange={clientChange}
                         type='text'/>
        </div>
        <div className={classes.fifthLine}>

          {Array.isArray(socialNetworks) &&
          socialNetworks.map((elem, i) => <TextFieldLite label="Соц.сеть"
                                                         required
                                                         size="small"
                                                         name="socialNetworks[0]"
                                                         variant="outlined"
                                                         value={elem}
                                                         onChange={(e) => networkChange(e, i)}
                                                         className={classes.clientSocialNetwork}
                                                         type='text'/>)
          }

        </div>

        <IconButton className={classes.addButton}>
          <PlusRoundButton addLine={addNetwork}/>
        </IconButton>
        <div className={classes.buttonSaveBlock}>
          <Button size='medium'
                  onClick={clickHandle}
                  disabled={saveButtonDisabled}
                  variant='contained'
                  className={classes.materialTypeButton}
          >Сохранить</Button>
        </div>
      </div>
  );
};

const useStyles = makeStyles((theme) => ({
  makeClientBlock: {
    flexBasis: '15%',
    border: '1px solid #a7a0a0',
    boxShadow: '0px 10px 15px -5px rgba(0,0,0,0.5)',
    padding: '1%',
    paddingTop: '0',
    flexWrap: 'wrap',
    maxWidth: '380px',
    height: 'fit-content',
    marginRight: '20px',
    marginLeft: '15px',
    ['@media (max-width:1300px)']: {
      flexBasis: '100%',
      maxWidth: 'initial',
      marginBottom: '20px',
    },
    ['@media (max-width:668px)']: {
      border: 'none',
      boxShadow: 'none',
      margin: '0 5px',
    },
  },
  clientName: {
    marginTop: '20px',
    width: '100%',
  },
  clientDate: {
    width: '40%',
    marginTop: '20px',
  },
  thirdLine: {
    display: 'flex',
    marginTop: '20px',
    justifyContent: 'space-between',
  },
  fourthLine: {
    display: 'flex',
    marginTop: '20px',
    justifyContent: 'space-between',
  },
  fifthLine: {
    display: 'flex',
    marginTop: '20px',
    justifyContent: 'space-between',
    alignItems: 'flex-start',
    flexDirection: 'column'
  },
  clientAddress: {
    flexBasis: '65%',
  },
  clientTime: {
    flexBasis: '30%',
  },
  clientPhone: {
    flexBasis: '65%',
  },
  clientSocialNetwork: {
    width: '65%',
    marginBottom: '10px'
  },
  buttonSaveBlock: {
    borderTop: '1px solid #c1bdbdde',
    marginTop: '60px',
    paddingTop: '20px',
    display: 'flex',
    justifyContent: 'flex-end',
  },
  materialTypeButton: {
    background: global.config.colors.purple,
    color: '#fff',
    borderRadius: '13px',
    justifyContent: 'center',
    alignItems: 'center',
    fontWeight: '300',
    padding: '1px 10px',
    cursor: 'pointer',
    '&:hover': {
      background: global.config.colors.purple,
      color: global.config.colors.gold,
    },
    [theme.breakpoints.down('xs')]: {
      display: 'flex',
    },
  },
  addButton: {
    float: 'right'
  }
}));

export default MakeOrderMakeClient;
